.introduction{
    padding: 16px 16px 0;
}

.introduction-content{
    color: #6B6B6B;
    font-size: 18px;
    margin-bottom: 16px;
}

.introduction-tag{
    color: #fff;
    background: -webkit-gradient(linear, left top, right top, from(#fea63f),to(#5ee5a9));
    background: -webkit-linear-gradient(left, #fea63f 0%,#5ee5a9 100%);
    background: -o-linear-gradient(left, #fea63f 0%,#5ee5a9 100%);
    background: linear-gradient(to right, #fea63f 0%,#5ee5a9 100%);
    margin-right: 8px;
    border-radius: 3px;
    font-size: 12px;
    padding: 2px;
    margin-top: 5px;
}


