.common{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    background-color: #fff;
    border-radius: 5px;
    margin-bottom: 20px;
}

.common-title{
    padding: 10px 20px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    border-bottom: 2px solid #f6f6f6;
}

.title-text{
    font-size: 3em;
    color: #080808;
    font-weight: 700;
}

.title-log{
    width: 8px;
    height: 2em;
    margin-right: 1em;
    background: -webkit-gradient(linear, left top, left bottom, from(#5ee5a6),to(#7db9e8));
    background: -webkit-linear-gradient(top, #5ee5a6 0%,#7db9e8 100%);
    background: -o-linear-gradient(top, #5ee5a6 0%,#7db9e8 100%);
    background: linear-gradient(to bottom, #5ee5a6 0%,#7db9e8 100%);
}




