*{
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

a{
  color: inherit;
  text-decoration: none;
}

ul,ol{
  list-style: none;
}

h1,h2,h3,h4,h5{
  font-weight: normal;
}

body {
  font-family: sans-serif;
  background-color: #f6f6f6;
}

@font-face {
  font-family: 'iconfont';  /* project id 809018 */
  src: url('//at.alicdn.com/t/font_809018_0sc03r5gb3w.eot');
  src: url('//at.alicdn.com/t/font_809018_0sc03r5gb3w.eot?#iefix') format('embedded-opentype'),
  url('//at.alicdn.com/t/font_809018_0sc03r5gb3w.woff') format('woff'),
  url('//at.alicdn.com/t/font_809018_0sc03r5gb3w.ttf') format('truetype'),
  url('//at.alicdn.com/t/font_809018_0sc03r5gb3w.svg#iconfont') format('svg');
}

.iconfont{
  font-family:"iconfont" !important;
  font-size:16px;font-style:normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;}

code{
  /*background: linear-gradient(to right, #03b9af 0%,#5ee5a9 100%);*/
  /*background: linear-gradient(to right, #03a6fe 0%,#5ee5a9 100%);*/
  color: #555;
  background-color: #eee;
  padding: 2px 4px;
  word-wrap: break-word;
  border-radius: 3px;
  font-size: inherit;
}



